___CSS_LOADER_EXPORT___.push([module.id, `.sidebar {
  background: var(--neutral-15);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 2rem;
  grid-row: span 2;
  padding-bottom: 1rem;

  .space-button {
    background: none;
    display: flex;
    justify-content: space-between;
    border-radius: 0;
    font-size: 1rem;
    font-style: normal;
    font-weight: 500;
    line-height: 1.5;
    gap: .5rem;
    padding: .5rem 1rem;
    width: 100%;
    color: var(--neutral-90);
    cursor: pointer;

    &:hover {
      .tasks-count {
        display: none;
      }

      .icon {
        display: block;
      }

      background: var(--neutral-20);
    }

    &[data-tasks="0"] {
      color: var(--neutral-50);
    }
  }

  .name-and-labels {
    display: flex;
    align-items: center;
    gap: .5rem;
    flex: 1;
    overflow: hidden;
  }

  .space-button-name {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .auto-tag {
    color: var(--light-blue);
    background: var(--light-blue-low);
  }

  .list-tag {
    color: var(--purple);
    background: var(--purple-low);
  }

  .icon {
    display: none;
    position: relative;
    left: .25rem;
  }
}`, "",{"version":3,"sources":["webpack://./src/components/sidebar/index.css"],"names":[],"mappings":"AAAA;EACE,6BAA6B;EAC7B,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,aAAa;EACb,gBAAgB;EAChB,oBAAoB;;EAEpB;IACE,gBAAgB;IAChB,aAAa;IACb,8BAA8B;IAC9B,gBAAgB;IAChB,eAAe;IACf,kBAAkB;IAClB,gBAAgB;IAChB,gBAAgB;IAChB,UAAU;IACV,mBAAmB;IACnB,WAAW;IACX,wBAAwB;IACxB,eAAe;;IAEf;MACE;QACE,aAAa;MACf;;MAEA;QACE,cAAc;MAChB;;MAEA,6BAA6B;IAC/B;;IAEA;MACE,wBAAwB;IAC1B;EACF;;EAEA;IACE,aAAa;IACb,mBAAmB;IACnB,UAAU;IACV,OAAO;IACP,gBAAgB;EAClB;;EAEA;IACE,gBAAgB;IAChB,mBAAmB;IACnB,uBAAuB;EACzB;;EAEA;IACE,wBAAwB;IACxB,iCAAiC;EACnC;;EAEA;IACE,oBAAoB;IACpB,6BAA6B;EAC/B;;EAEA;IACE,aAAa;IACb,kBAAkB;IAClB,YAAY;EACd;AACF","sourcesContent":[".sidebar {\n  background: var(--neutral-15);\n  display: flex;\n  flex-direction: column;\n  gap: 1rem;\n  padding: 2rem;\n  grid-row: span 2;\n  padding-bottom: 1rem;\n\n  .space-button {\n    background: none;\n    display: flex;\n    justify-content: space-between;\n    border-radius: 0;\n    font-size: 1rem;\n    font-style: normal;\n    font-weight: 500;\n    line-height: 1.5;\n    gap: .5rem;\n    padding: .5rem 1rem;\n    width: 100%;\n    color: var(--neutral-90);\n    cursor: pointer;\n\n    &:hover {\n      .tasks-count {\n        display: none;\n      }\n\n      .icon {\n        display: block;\n      }\n\n      background: var(--neutral-20);\n    }\n\n    &[data-tasks=\"0\"] {\n      color: var(--neutral-50);\n    }\n  }\n\n  .name-and-labels {\n    display: flex;\n    align-items: center;\n    gap: .5rem;\n    flex: 1;\n    overflow: hidden;\n  }\n\n  .space-button-name {\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n  }\n\n  .auto-tag {\n    color: var(--light-blue);\n    background: var(--light-blue-low);\n  }\n\n  .list-tag {\n    color: var(--purple);\n    background: var(--purple-low);\n  }\n\n  .icon {\n    display: none;\n    position: relative;\n    left: .25rem;\n  }\n}"],"sourceRoot":""}]);
