___CSS_LOADER_EXPORT___.push([module.id, `.task {
  display: flex;
  flex-direction: column;
  gap: .25rem;
  padding: .25rem .5rem;

  &:hover {
    .icon:not(.tick-icon) {
      display: block;
    }
  }

  .task-body {
    display: flex;
    gap: .5rem;
    align-items: center;
  }

  p.large {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .priority-bubble {
    position: relative;
    width: 1.5rem;
    height: 1.5rem;
    padding: 0;
    border: 2px solid var(--neutral-50);
    background: transparent;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    .tick-icon {
      position: absolute;
      display: none;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
    }
  }

  &[data-completed="true"] {
    text-decoration: line-through;
    color: var(--neutral-50);

    p.desc, .tag {
      display: none;
    }
  }

  &[data-completed="true"] .priority-bubble, .priority-bubble:hover {
    .tick-icon {
      display: block;
    }
  }

  .priority-high {
    background-color: var(--red-low);
    border-color: var(--red);
    svg {
      fill: var(--red);
    }
  }

  .priority-medium {
    background-color: var(--yellow-low);
    border-color: var(--yellow);
    svg {
      fill: var(--yellow);
    }
  }

  .priority-low {
    background-color: var(--light-blue-low);
    border-color: var(--light-blue);
    svg {
      fill: var(--light-blue);
    }
  }

  .priority-none {
    svg {
      fill: var(--neutral-50);
    }
  }

  .overdue-tag {
    color: var(--red);
    background: var(--red-low);
  }

  .yesterday-tag {
    color: var(--orange);
    background: var(--orange-low);
  }

  .today-tag {
    color: var(--accent);
    background: var(--accent-low);
  }

  .tomorrow-tag {
    color: var(--purple);
    background: var(--purple-low);
  }

  .this-week-tag {
    color: var(--yellow);
    background: var(--yellow-low);
  }

  .after-this-week-tag {
    color: var(--light-blue);
    background: var(--light-blue-low);
  }

  .icon:not(.tick-icon) {
    display: none;
    cursor: pointer;
  }

  .edit-icon {
    margin-left: auto;
  }

  p.desc {
    overflow-wrap: break-word;
  }
}`, "",{"version":3,"sources":["webpack://./src/components/task/index.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;EACX,qBAAqB;;EAErB;IACE;MACE,cAAc;IAChB;EACF;;EAEA;IACE,aAAa;IACb,UAAU;IACV,mBAAmB;EACrB;;EAEA;IACE,gBAAgB;IAChB,mBAAmB;IACnB,uBAAuB;EACzB;;EAEA;IACE,kBAAkB;IAClB,aAAa;IACb,cAAc;IACd,UAAU;IACV,mCAAmC;IACnC,uBAAuB;IACvB,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,cAAc;;IAEd;MACE,kBAAkB;MAClB,aAAa;MACb,QAAQ;MACR,SAAS;MACT,4CAA4C;IAC9C;EACF;;EAEA;IACE,6BAA6B;IAC7B,wBAAwB;;IAExB;MACE,aAAa;IACf;EACF;;EAEA;IACE;MACE,cAAc;IAChB;EACF;;EAEA;IACE,gCAAgC;IAChC,wBAAwB;IACxB;MACE,gBAAgB;IAClB;EACF;;EAEA;IACE,mCAAmC;IACnC,2BAA2B;IAC3B;MACE,mBAAmB;IACrB;EACF;;EAEA;IACE,uCAAuC;IACvC,+BAA+B;IAC/B;MACE,uBAAuB;IACzB;EACF;;EAEA;IACE;MACE,uBAAuB;IACzB;EACF;;EAEA;IACE,iBAAiB;IACjB,0BAA0B;EAC5B;;EAEA;IACE,oBAAoB;IACpB,6BAA6B;EAC/B;;EAEA;IACE,oBAAoB;IACpB,6BAA6B;EAC/B;;EAEA;IACE,oBAAoB;IACpB,6BAA6B;EAC/B;;EAEA;IACE,oBAAoB;IACpB,6BAA6B;EAC/B;;EAEA;IACE,wBAAwB;IACxB,iCAAiC;EACnC;;EAEA;IACE,aAAa;IACb,eAAe;EACjB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,yBAAyB;EAC3B;AACF","sourcesContent":[".task {\n  display: flex;\n  flex-direction: column;\n  gap: .25rem;\n  padding: .25rem .5rem;\n\n  &:hover {\n    .icon:not(.tick-icon) {\n      display: block;\n    }\n  }\n\n  .task-body {\n    display: flex;\n    gap: .5rem;\n    align-items: center;\n  }\n\n  p.large {\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n  }\n\n  .priority-bubble {\n    position: relative;\n    width: 1.5rem;\n    height: 1.5rem;\n    padding: 0;\n    border: 2px solid var(--neutral-50);\n    background: transparent;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-shrink: 0;\n\n    .tick-icon {\n      position: absolute;\n      display: none;\n      top: 50%;\n      left: 50%;\n      transform: translateX(-50%) translateY(-50%);\n    }\n  }\n\n  &[data-completed=\"true\"] {\n    text-decoration: line-thr