___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --neutral-10:  hsl(0deg 0% 10%);
  --neutral-15:  hsl(0deg 0% 15%);
  --neutral-20:  hsl(0deg 0% 20%);
  --neutral-30:  hsl(0deg 0% 30%);
  --neutral-50:  hsl(0deg 0% 50%);
  --neutral-70:  hsl(0deg 0% 70%);
  --neutral-90:  hsl(0deg 0% 90%);

  --accent: hsl(166deg 90% 60%);
  --accent-low: hsl(166deg 90% 60% / 25%);
  --accent-very-low: hsl(166deg 90% 60% / 5%);

  --red: hsl(0deg 90% 60%);
  --red-lite: hsl(0deg 90% 70%);
  --red-low: hsl(0deg 90% 60% / 25%);

  --orange: hsl(20deg 90% 60%);
  --orange-low: hsl(20deg 90% 60% / 25%);

  --yellow: hsl(40deg 90% 60%);
  --yellow-low: hsl(40deg 90% 60% / 25%);

  --light-blue: hsl(200deg 90% 60%);
  --light-blue-low: hsl(200deg 90% 60% / 25%);

  --purple: hsl(260deg 100% 70%);
  --purple-low: hsl(260deg 100% 70% / 25%);
}

body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: grid;
  grid-template-columns: 40ch 3fr;
  grid-template-rows: 1fr min-content;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 500;
  font-style: normal;
  color: var(--neutral-90);
  background: var(--neutral-10);
}

h1, h2, h3, h4, h5, h6, button {
  font-weight: 700;
  line-height: 1.15;
}

hr {
  height: 1px;
  border: none;
  background-color: var(--neutral-50);
}

h1.special {
  font-size: 3rem;
  font-weight: 200;
  span {
    vertical-align: text-top;
  }
}

p.large {
  font-size: 1.3rem;
  font-weight: 300;
}

p.desc {
  font-weight: 400;
  color: var(--neutral-50);
}

button {
  border: none;
  border-radius: .5rem;
  padding: 1rem 2rem;
  font-size: 1.3rem;
  cursor: pointer;
  color: var(--neutral-10);
  background: var(--neutral-90);

  &:hover {
    background: var(--neutral-70);
  }

  &.special-button {
    background: none;
    border: 1px solid var(--accent);
    color: var(--accent);
    &:hover {
      background: var(--accent);
      color: var(--neutral-10);
    }
  }

  &.cancel-button {
    background: none;
    border: 1px solid var(--neutral-50);
    color: var(--neutral-50);
    &:hover {
      border-color: var(--neutral-70);
      color: var(--neutral-70);
    }
  }
  
  &.delete-button {
    background: var(--red);
    color: var(--neutral-90);
    &:hover {
      background: var(--red-lite);
    }
  }
}

dialog {
  width: 60ch;
  padding: 3rem;
  background: var(--neutral-15);
  border: none;
  outline: none;
  border-radius: 1rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  color: inherit;

  &::backdrop {
    background: #00000080;
  }
}

dialog > form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

input {
  display: block;
  width: 100%;
  color: inherit;

  &:autofill { /* ! Not working */
    border-color: var(--light-blue);
    background: none;
  }
}

input:not([type="range"], [type="radio"]), textarea, select {
  background: none;
  border-radius: .5rem;
  padding: .25rem .5rem;
  border: 1px solid var(--neutral-50);

  &:focus {
    border-color: var(--neutral-90);
    outline: none;
  }
}

::-webkit-calendar-picker-indicator {
  filter: invert(1);
}

input:disabled {
  border-color: var(--neutral-30);
  color: var(--neutral-30);
  cursor: not-allowed;
}

textarea {
  color: inherit;
  resize: none;
  height: 6lh;
}

select, option {
  background: var(--neutral-15);
  color: inherit;
}

form > section {
  display: flex;
  flex-direction: column;
  gap: .5rem;
}

form .buttons {
  display: flex;
  justify-content: right;
  gap: 1rem;
}

label {
  font-size: 1.5rem;
  line-height: 1.15;
}

label .additional {
  font-size: 0.85rem;
  font-weight: 400;
  line-height: 1.5;
  color: var(--neutral-50);
}

.tag {
  padding: 0 1rem;
  border-radius: .5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.credits {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--neutral-50);
}

.icon {
  flex-shrink: 0;
  background: none;
  padding: 0;
  svg {
    fill: var(--neutral-50);
  }

  &:hover {
    background: none;
    svg {
      fill: var(--neutral-90);
    }
  }
}

.icon.blocked-icon {
  svg {
    fill: var(--neutral-30);
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;EACE,+BAA+B;EAC/B,+BAA+B;EAC/B,+BAA+B;EAC/B,+BAA+B;EAC/B,+BAA+B;EAC/B,+BAA+B;EAC/B,+BAA+B;;EAE/B,6BAA6B;EAC7B,uCAAuC;EACvC,2CAA2C;;EAE3C,wBAAwB;EACxB,6BAA6B;EAC7B,kCAAkC;;EAElC,4BAA4B;EAC5B,sCAAsC;;EAEtC,4BAA4B;EAC5B,sCAAsC;;EAEtC,iCAAiC;EACjC,2CAA2C;;EAE3C,8BAA8B;EAC9B,wCAAwC;AAC1C;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,gBAAgB;EAChB,aAAa;EACb,+BAA+B;EAC/B,mCAAmC;EACnC,gCAAgC;EAChC,eAAe;EACf,gBAAgB;EAChB,kBAAkB;EAClB,wBAAwB;EACxB,6BAA6B;AAC/B;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,mCAAmC;AACrC;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB;IACE,wBAAwB;EAC1B;AACF;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,wBAAwB;AAC1B;;AAEA;EACE,YAAY;EACZ,oBAAoB;EACpB,kBAAkB;EAClB,iBAAiB;EACjB,eAAe;EACf,wBAAwB;EACxB,6BAA6B;;EAE7B;IACE,6BAA6B;EAC/B;;EAEA;IACE,gBAAgB;IAChB,+BAA+B;IAC/B,oBAAoB;IACpB;MACE,yBAAyB;MACzB,wBAAwB;IAC1B;EACF;;EAEA;IACE,gBAAgB;IAChB,mCAAmC;IACnC,wBAAwB;IACxB;MACE,+BAA+B;MAC/B,wBAAwB;IAC1B;EACF;;EAEA;IACE,sBAAsB;IACtB,wBAAwB;IACxB;MACE,2BAA2B;IAC7B;EACF;AACF;;AAEA;EACE,WAAW;EACX,aAAa;EACb,6BAA6B;EAC7B,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,4CAA4C;EAC5C,cAAc;;EAEd;IACE,qBAAqB;EACvB;AACF;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,cAAc;EACd,WAAW;EACX,cAAc;;EAEd,aAAa,kBAAkB;IAC7B,+BAA+B;IAC/B,gBAAgB;EAClB;AACF;;AAEA;EACE,gBAAgB;EAChB,oBAAoB;EACpB,qBAAqB;EACrB,mCAAmC;;EAEnC;IACE,+BAA+B;IAC/B,aAAa;EACf;AACF;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,+BAA+B;EAC/B,wBAAwB;EACxB,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,6BAA6B;EAC7B,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,UAAU;AACZ;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,gBAAgB;EAChB,wBAAwB;AAC1B;;AAEA;EACE,eAAe;EACf,oBAAoB;EACpB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,wBAAwB;AAC1B;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,UAAU;EACV;IA